@import '../helpers/helpers-all.scss';

.contact_section {
	position: relative;

	border-top: 4px solid $black;
	background-color: $yellow;

	@include bp($point_2, min) {
		padding-top: 7rem;
		padding-bottom: 9.1rem;
	}

	@include bp($point_3) {
		padding-top: 3rem;
		padding-bottom: 3rem;
	}
}

.contact_section__in {
	@include bp($point_2, min) {
		display: flex;
		flex-direction: row;
		align-items: center;
	}
}

.contact_section__header {
	margin-right: 5rem;
	position: relative;

	text-transform: uppercase;
	font-family: $raleway;
	font-size: 3.5rem;
	font-weight: 700;
	color: rgba($black, 0.7);

	@include bp($point_3) {
		margin-right: auto;
		margin-bottom: 5.2rem;
	}
}

.contact_section__link {
	position: relative;
	margin-left: auto;

	font-weight: 400;
	text-transform: uppercase;
	text-decoration-line: underline;
	color: rgba($black, 0.7);
	transition: color 0.3s ease-in-out;

	&:hover {
		color: rgba($black, 1);
	}

	@include bp($point_2, min) {
		font-size: 6.3rem;
	}

	@include bp($point_3) {
		font-size: 5rem;
	}
}
